import React, { useState, useEffect } from "react";
import { Form, Modal, Input, Select, Tooltip, Radio } from "antd";
import { connect } from "react-redux";
import snackbar from "@utils/snackbar-utils";
import dateUtils from "mainam-react-native-date-utils";
import actionDmMauDungCu from "@actions/dm-mau-dung-cu";
import actionDmDungCu from "@actions/dm-dung-cu";
import "../style.scss";
const { Option } = Select;
const { TextArea } = Input;
function index(props) {

  const [state, _setState] = useState({
    checkValidate: false,
  });

  const setState = (data = {}) => {
    _setState((state) => {
      return { ...state, ...data };
    });
  };
  useEffect(() => {
    props.loadList("", "", "DUNG_CU_Y_TE");
    props.updateData({
      checkValidate: false,
      checkValue: false,
      checkName: false,
    })
  }, []);

  const { getFieldDecorator } = props.form;
  
  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        props.createOrEdit().then(s => {
        });
      }
    });
  };
  const closeModal = () => {
    props.updateData({
      id: "",
      name: "",
      value: "",
      dungCuIds: [],
      active: false,
      isOpen: false,
    })
  }
  return (
    <Modal
      className="modal-popup"
      width={500}
      title={props.id ? "Cp nht danh mc mu dng c y t" : "Thm mi danh mc mu dng c y t"}
      visible={true}
      cancelText={"ng"}
      onOk={handleSubmit}
      okText={props.id ? "Cp nht" : "Thm mi"}
      onCancel={closeModal}
    >
      <Form layout="vertical" hideRequiredMark onSubmit={handleSubmit}>
        <div>
          <Form.Item label="M s *">
          {getFieldDecorator("value", {
              rules: [
                {
                  required: true,
                  message: "Vui lng nhp m s!"
                }
              ],
              initialValue: props.value
            })(
            <Input
              style={props.id ? {  backgroundColor: '#f5f5ef' } : {  }}
              readOnly={props.id}
              onChange={(event) => {
                props.updateData({
                  value: event.target.value
                });
              }}
              placeholder="Nhp m s"
            />
            )}
          </Form.Item>
        </div>
        <div>
          <Form.Item label="Tn mu dng c y t *">
          {getFieldDecorator("name", {
              rules: [
                {
                  required: true,
                  message: "Vui lng nhp tn mu dng c y t!"
                }
              ],
              initialValue: props.name
            })(
            <Input
              onChange={(event) => {
                props.updateData({
                  name: event.target.value
                });
              }}
              placeholder="Nhp tn mu"
            />
            )}
          </Form.Item>
        </div>
        <div>
          <Form.Item label="Tn dng c y t *">
          {getFieldDecorator("dungCuIds", {
              rules: [
                {
                  required: true,
                  message: "Vui lng chn tn dng c y t!"
                }
              ],
              initialValue: props.dungCuIds
            })(
            <Select
              mode='multiple'
              showSearch
              onChange={e => props.updateData({
                dungCuIds: e.map(item => Number(item))
              })}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase().unsignText()
                  .indexOf(input.toLowerCase().unsignText()) >= 0
              }
              placeholder='Chn tn dng c y t'
            >
              {props.dmDungcu.map((item, index) => {
                return (
                  <Option key={index} value={item.id}>
                    {item.name}
                  </Option>
                );
              })}
            </Select>
            )}
          </Form.Item>
        </div>
      </Form>
    </Modal>
  );
}

export default connect(
  state => {
    return {
      auth: state.auth.auth,
      data: state.dmMauDungCu.data || [],
      id: state.dmMauDungCu.id,
      name: state.dmMauDungCu.name,
      value: state.dmMauDungCu.value,
      dungCuIds: state.dmMauDungCu.dungCuIds || [],
      dungCu: state.dmMauDungCu.dungCu,
      dmDungcu: state.dmDungcu.dmDungCu || [],
      checkValue: state.dmMauDungCu.checkValidate,
      checkName: state.dmMauDungCu.checkName,
      checkValue: state.dmMauDungCu.checkValue,
      checkValidate: state.dmMauDungCu.checkValidate,
    };
  }, {
  updateData: actionDmMauDungCu.updateData,
  createOrEdit: actionDmMauDungCu.createOrEdit,
  loadList: actionDmDungCu.loadList,
}
)(Form.create()(index));
