function createOrEdit() {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      let id = getState().dmMauDungCu.id;
      let active = true;
      let name = getState().dmMauDungCu.name;
      let value = getState().dmMauDungCu.value;
      let dungCuIds = getState().dmMauDungCu.dungCuIds;
      dmMauDungCuProvider
        .createOrEdit(id, active, name, value, dungCuIds)
        .then(s => {
          if (s.code == 0) {
            dispatch(
              updateData({
                id: "",
                name: "",
                value: "",
                dungCuIds: [],
                active: false,
                isOpen: false,
              })
            );
            if (!id) {
              snackbar.show("Thm mu dng c y t thnh cng", "success");
            } else {
              snackbar.show("Cp nht mu dng c y t thnh cng", "success");
            }
            dispatch(gotoPage(0));
            resolve(s.data);
          }
          else if (s.code == 1303) {
            snackbar.show(`M s ${value}  tn ti. Vui lng kim tra li!`, "danger");
            reject();
          }
          else if (s.code == 1302) {
            snackbar.show(`${name}  tn ti. Vui lng kim tra li!`, "danger");
            reject();
          }
          else {
            if (!id) {
              snackbar.show(s.message || "Thm mu dng c y t khng thnh cng", "danger");
            } else {
              snackbar.show(s.message || "Sa mu dng c y t khng thnh cng", "danger");
            }
            reject();
          }
        })
        .catch(e => {
          snackbar.show("Thm mu dng c y t khng thnh cng", "danger");
          reject();
        });
    });
  };
}

function onDeleteItem(item) {
  return (dispatch, getState) => {
    return new Promise((resolve, reject) => {
      confirm({
        title: "Xc nhn",
        content: `Bn c mun xa mu dng c y t ${item.name}?`,
        okText: "Xa",
        okType: "danger",
        cancelText: "Hy",
        onOk() {
          dmMauDungCuProvider
            .delete(item.id)
            .then(s => {
              if (s.code == 0) {
                snackbar.show("Xa mu dng c y t thnh cng", "success");
                let data = getState().dmMauDungCu.data || [];
                let index = data.findIndex(x => x.id == item.id);
                if (index != -1);
                data.splice(index, 1);
                dispatch(
                  updateData({
                    data: [...data]
                  })
                );
                dispatch(gotoPage(0));
                resolve();
              }
              else if (s.code === 605) {
                snackbar.show(`${item.name}  c s dng. Vui lng kim tra li!`, "danger");
                reject();
              }
              else {
                snackbar.show("Xa mu dng c y t khng thnh cng", "danger");
                reject();
              }
            })
            .catch(e => {
              snackbar.show("Xa mu dng c y t khng thnh cng", "danger");
              reject();
            });
        },
        onCancel() {
          reject();
        }
      });
    });
  };
}

export default {
  loadList,
  createOrEdit,
  updateData,
  gotoPage,
  onSearch,
  onSizeChange,
  onDeleteItem,
  loadDetail,
};
