import React, { useState, useEffect } from "react";
import { Button, Select, Tooltip, DatePicker } from "antd";
import { connect } from "react-redux";
import actionDmMauDungCu from "@actions/dm-mau-dung-cu";
import snackbarUtils from "@utils/snackbar-utils";
import dateUtils from "mainam-react-native-date-utils";
import Table from "@admin/components/common/Table";
import SelectSize from "@admin/components/common/SelectSize";
import Pagination from "@admin/components/common/Pagination";
import { AdminPage, Panel } from "@admin/components/admin";
import ModalMedical from "./create"
import './style.scss'
import moment from 'moment';

const { Option } = Select;

function index(props) {
  useEffect(() => {
    props.updateData({
      searchValue: "",
      searchName: "",
    })
    props.onSearch("", "");
  }, []);
  const onSizeChange = size => {
    props.onSizeChange(size);
  };

  const onPageChange = page => {
    props.gotoPage(page);
  };

  let data = (props.data || []).map((item, index) => {
    return {
      key: index,
      col1: (props.page - 1) * props.size + index + 1,
      col2: item.value,
      col3: item.name,
      col4: item.createdAt && new Date(item.createdAt).format("dd-MM-YYYY"),
      col5: item,
    };
  });

  const editItem = (item) => {
    props.updateData({
      id: item.id,
      active: item.active,
      name: item.name,
      value: item.value,
      dungCuIds: item.dungCuIds,
      isOpen: true,
    });
  };

  const onDeleteItem = item => {
    props.onDeleteItem(item);
  };

  const onCreate = (data) => {
    if (data) {
      props.updateData({
        ...data,
        isOpen: true,
      })
    } else {
      props.updateData({
        isOpen: true,
      })
    }
  }

  return (
    <>
      {
        // (props.auth.roleId === 1 || props.auth.roleId === 2) ?
          <>
            <AdminPage
              className="mgr-air-pollution-incidents"
              icon="subheader-icon fal fa-window"
              header="Danh mc mu dng c y t"
              subheader="Danh mc mu dng c y t"
            >
              <Panel
                id={"mgr-air-pollution-incidents"}
                title="Danh mc mu dng c y t"
                allowClose={false}
                allowCollapse={false}
                toolbar={
                  <div className="toolbar">
                    <Button className="button" onClick={() => onCreate()}>Thm mi</Button>
                  </div>
                }
              >
                <Table
                  scroll={{ x: 800, y: 500 }}
                  style={{ marginLeft: -10, marginRight: -10 }}
                  className="custom"
                  columns={[
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">STT</div>
                          <div className="addition-box"
                            style={{
                              display: "flex",
                              alignItems: "center",
                              color: "#000"
                            }}
                          >
                            LC THEO
                  </div>
                        </div>
                      ),
                      width: 100,
                      dataIndex: "col1",
                      key: "col1"
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">M s</div>
                          <div className="addition-box">
                            <div className="search-box">
                              <img src={require("@images/icon/ic-search.png")} alt="" />
                              <input
                                value={props.searchValue}
                                onChange={e => {
                                  props.updateData({
                                    searchValue: e.target.value
                                  })
                                  if (props.clearTimeOutAffterRequest) {
                                    try {
                                      clearTimeout(props.clearTimeOutAffterRequest);
                                    } catch (error) { }
                                  }
                                  let data = setTimeout(() => {
                                    props.gotoPage()
                                  }, 500)
                                  props.updateData({
                                    clearTimeOutAffterRequest: data
                                  })
                                }
                                }
                                placeholder="Tm theo m s"
                              />
                            </div>
                          </div>
                        </div>
                      ),
                      width: 250,
                      dataIndex: "col2",
                      key: "col2"
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Tn mu dng c y t</div>
                          <div className="addition-box">
                            <div className="search-box">
                              <img src={require("@images/icon/ic-search.png")} alt="" />
                              <input
                                value={props.searchName}
                                onChange={e => {
                                  props.updateData({
                                    searchName: e.target.value
                                  })
                                  if (props.clearTimeOutAffterRequest) {
                                    try {
                                      clearTimeout(props.clearTimeOutAffterRequest);
                                    } catch (error) { }
                                  }
                                  let data = setTimeout(() => {
                                    props.gotoPage()
                                  }, 500)
                                  props.updateData({
                                    clearTimeOutAffterRequest: data
                                  })
                                }
                                }
                                placeholder="Tm theo tn sn phm"
                              />
                            </div>
                          </div>
                        </div>
                      ),
                      width: 350,
                      dataIndex: "col3",
                      key: "col3",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box">Ngy to</div>
                          <div className="addition-box"></div>
                        </div>
                      ),
                      width: 400,
                      dataIndex: "col4",
                      key: "col4",
                    },
                    {
                      title: (
                        <div className="custome-header">
                          <div className="title-box"></div>
                          <div className="addition-box"></div>
                        </div>
                      ),
                      key: "col5",
                      fixed: "right",
                      dataIndex: "col5",
                      width: 100,
                      render: item => {
                        return (
                          <div className="col-action">
                            <Tooltip placement="topLeft" title={"Sa"}>
                              <div>
                                <a
                                  onClick={() => editItem(item)}
                                  className="btn btn-info btn-icon waves-effect waves-themed"
                                >
                                  <i className="fal fa-edit"></i>
                                </a>
                              </div>
                            </Tooltip>
                            <Tooltip placement="topLeft" title={"Xa"}>
                              <div>
                                <a
                                  onClick={() => onDeleteItem(item)}
                                  className="btn btn-info btn-icon waves-effect waves-themed"
                                >
                                  <i className="fal fa-trash-alt"></i>
                                </a>
                              </div>
                            </Tooltip>
                          </div>
                        );
                      }
                    }
                  ]}
                  dataSource={data}
                ></Table>
                <div className="footer">
                  <SelectSize value={props.size} selectItem={onSizeChange} />
                  <Pagination
                    onPageChange={onPageChange}
                    page={props.page}
                    size={props.size}
                    total={props.total}
                    style={{ flex: 1, justifyContent: "flex-end" }}
                  />
                </div>
              </Panel>
            </AdminPage>

            {
              props.isOpen ?
                <ModalMedical />
                : null
            }
          </>
          // : null
      }

    </>
  );
}

export default connect(
  state => {
    return {
      auth: state.auth.auth,
      data: state.dmMauDungCu.data || [],
      size: state.dmMauDungCu.size || 10,
      page: state.dmMauDungCu.page || 1,
      total: state.dmMauDungCu.total || 0,
      isOpen: state.dmMauDungCu.isOpen || false,
      dungCuIds: state.dmMauDungCu.dungCuIds,
      searchValue: state.dmMauDungCu.searchValue,
      searchName: state.dmMauDungCu.searchName,
      dataMedical: [],
      clearTimeOutAffterRequest: state.dmMauDungCu.clearTimeOutAffterRequest || null
    };
  },
  {
    updateData: actionDmMauDungCu.updateData,
    onSizeChange: actionDmMauDungCu.onSizeChange,
    gotoPage: actionDmMauDungCu.gotoPage,
    onSearch: actionDmMauDungCu.onSearch,
    onDeleteItem: actionDmMauDungCu.onDeleteItem,
  }
)(index);

